.kartya {
    background-color: #1a1a1a; 
    border: 2px solid #ff0000; 
    border-radius: 10px; 
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.6); 
    color: #fff;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    padding: 20px;
    margin: 5px;
    text-align: left;
    width: 200px;
  }
  
  .kartya:hover {
    transform: scale(1.05);
  }
  
  .kartya img {
    width: 100px;
    height: auto;
    margin-bottom: 15px;
  }
  
  .kartya h2 {
    font-size: 1rem;
    margin-bottom: 10px;
  }
  
  .kartya p {
    font-size: 1rem;
    margin-bottom: 15px;
  }
  
  .kartya button {
    background-color: #ff0000;
    border: none;
    padding: 10px 20px;
    color: #fff;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .kartya button:hover {
    background-color: #990000;
  }
  h3{
    font-size: 2rem;
    margin-bottom: 10px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    margin: 10px;
    color: #9a031e;
  }
  
  ul{
  align-items: center;
  }
  
  .kosar{
  background-color: rgb(255,255,255, 0.4);
  }
  
  li{
    list-style-type: none;
    padding: 5px;
    background-color: #1a1a1a;
    color: white;
    width: auto;
    margin-block: 5px;
    border-radius: 10px;
  }
  
  .delBtn {
    background-color: #ff0000;
    border: none;
    padding: 5px 10px;
    color: #fff;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
    border-radius: 10px;
    margin-top: 3px;
  }
  
  .delBtn:hover {
    background-color: #990000;
  }